import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { JobStatusHomeComponent } from "./job-status-home/job-status-home.component";
import { SubmitEdrJobComponent } from "./submit-edr-job/submit-edr-job.component";
import { ScheduledJobListComponent } from "./scheduled-job-list/scheduled-job-list.component";
import { ScheduledJobEditComponent } from "./scheduled-job-edit/scheduled-job-edit.component";
import { HomeJobReportComponent } from "./home-job-report/home-job-report.component";

const routes: Routes = [
  { path: "", redirectTo: "/jobstatus", pathMatch: "full" },
  { path: "jobstatus", component: JobStatusHomeComponent },
  { path: "submittedjob", component: SubmitEdrJobComponent },
  { path: "scheduledjob", component: ScheduledJobListComponent },
  { path: "scheduledjobedit", component: ScheduledJobEditComponent },
  { path: "homejobreport", component: HomeJobReportComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
export const RoutingComponents = [
  JobStatusHomeComponent,
  SubmitEdrJobComponent,
  ScheduledJobListComponent,
  ScheduledJobEditComponent,
  HomeJobReportComponent
];
