import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { IScheduledJobModel } from "../model/scheduledJob.model";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class ScheduledJobListService {
  private _url: string = "/assets/data/scheduledData.json";

  constructor(private http: HttpClient) {}

  getScheduledJobList(): Observable<IScheduledJobModel[]> {
    return this.http.get<IScheduledJobModel[]>(this._url);
  }
}
