import { Component, OnInit } from "@angular/core";
import { EdrModel } from "./edr-model";
import { SubmitEdrDataService } from "../services/submit-edr-data.service";

@Component({
  selector: "app-submit-edr-job",
  templateUrl: "./submit-edr-job.component.html",
  styleUrls: ["./submit-edr-job.component.css"]
})
export class SubmitEdrJobComponent implements OnInit {
  //model: any = {};
  dayofweekSelector: boolean = false;
  dayofmonthSelector: boolean = false;

  model = new EdrModel("", "", "", "", "", "", "", "", "", "", "");

  constructor(private _submitedrdataservice: SubmitEdrDataService) {}

  edrJobSubmit(submitEdrJob) {
    console.log(submitEdrJob.value);
    this._submitedrdataservice
      .submitEdrJobData(this.model)
      .subscribe(
        data => console.log("!Success", data),
        error => console.error("Error", error)
      );

    //Code for triggering Reset button after Form submit to reset the Form
    let element: HTMLElement = document.getElementById(
      "resetButton"
    ) as HTMLElement;
    element.click();
  }

  //Function for toggling between
  //dayofWeek and dayofMonth select fields based upon Frequency select
  weekSelector(value) {
    if (value === "Weekly") {
      this.dayofweekSelector = true;
      this.dayofmonthSelector = false;
    } else if (value === "Monthly") {
      this.dayofmonthSelector = true;
      this.dayofweekSelector = false;
    } else {
      this.dayofweekSelector = false;
      this.dayofmonthSelector = false;
      this.model.dayofWeek = "";
      this.model.dayofMonth = "";
    }
  }
  ngOnInit() {}
}
