import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { IHomeJobReport } from "../model/homejobReport.model";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class HomeJobReportService {
  private _url: string = "/assets/data/homejobReport.json";

  constructor(private http: HttpClient) {}

  getJobStatus(): Observable<IHomeJobReport[]> {
    return this.http.get<IHomeJobReport[]>(this._url);
  }
}
