import { Component, OnInit } from "@angular/core";
import { ScheduledJobListService } from "../services/scheduled-job-list.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-scheduled-job-list",
  templateUrl: "./scheduled-job-list.component.html",
  styleUrls: ["./scheduled-job-list.component.css"]
})
export class ScheduledJobListComponent implements OnInit {
  name = "Angular";
  public scheduledjobData = [];
  public pageSize = 5;
  sortedCollection: any[];

  constructor(
    private _scheduledjoblistservice: ScheduledJobListService,
    private router: Router
  ) {}

  checkoptions = function() {
    if (this.jobRadio != undefined) {
      this.msg = "Selected Value: " + this.jobRadio;
      this.router.navigateByUrl("/scheduledjobedit", {
        state: { data: this.jobRadio }
      });
    } else {
      this.msg = "Please choose an option";
    }
  };

  deleteJob = function() {
    let str = this.jobRadio;
    let jsonData = JSON.parse(str);
    //console.log(jsonData);
    alert("Please confirm if want to delete " + jsonData.jobName);
  };

  ngOnInit() {
    this._scheduledjoblistservice.getScheduledJobList().subscribe(
      data => (this.scheduledjobData = data),
      (error: Response) => {
        if (error.status === 404) {
          alert("This data is not available on server");
        } else {
          alert("An unexpected error occurred");
        }
      }
    );
  }
}
