import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { EdrModel } from "../submit-edr-job/edr-model";

@Injectable({
  providedIn: "root"
})
export class SubmitEdrDataService {
  _url = "";
  constructor(private _http: HttpClient) {}

  submitEdrJobData(edrmodel: EdrModel) {
    return this._http.post<any>(this._url, edrmodel);
  }
}
