import { Component, OnInit } from "@angular/core";
import { JobStatusService } from "../services/job-status.service";
import { OrderPipe } from "ngx-order-pipe";

@Component({
  selector: "app-job-status-home",
  templateUrl: "./job-status-home.component.html",
  styleUrls: ["./job-status-home.component.css"]
})
export class JobStatusHomeComponent implements OnInit {
  public jobstatusData = [];
  public pageSize = 5;
  order: string = "jobKey";
  reverse: boolean = false;

  sortedCollection: any[];

  constructor(
    private orderPipe: OrderPipe,
    private _jobstatusservice: JobStatusService
  ) {
    this.sortedCollection = orderPipe.transform(this.jobstatusData, "jobKey");
    //console.log(this.sortedCollection);
  }

  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }

    this.order = value;
  }

  ngOnInit() {
    this._jobstatusservice.getJobStatus().subscribe(
      data => (this.jobstatusData = data),
      (error: Response) => {
        if (error.status === 404) {
          alert("This data is not available on server");
        } else {
          alert("An unexpected error occurred");
        }
      }
    );
  }
}
