import { Component, OnInit } from "@angular/core";
import { HomeJobReportService } from "../services/home-job-report.service";
import { OrderPipe } from "ngx-order-pipe";

@Component({
  selector: "app-home-job-report",
  templateUrl: "./home-job-report.component.html",
  styleUrls: ["./home-job-report.component.css"]
})
export class HomeJobReportComponent implements OnInit {
  public homejobreportData = [];
  public pageSize = 5;
  order: string = "name";
  reverse: boolean = false;

  sortedCollection: any[];

  constructor(
    private orderPipe: OrderPipe,
    private _homejobreportservice: HomeJobReportService
  ) {
    this.sortedCollection = orderPipe.transform(
      this.homejobreportData,
      "jobKey"
    );
    //console.log(this.sortedCollection);
  }

  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }

    this.order = value;
  }

  ngOnInit() {
    this._homejobreportservice.getJobStatus().subscribe(
      data => (this.homejobreportData = data),
      (error: Response) => {
        if (error.status === 404) {
          alert("This data is not available on server");
        } else {
          alert("An unexpected error occurred");
        }
      }
    );
  }
}
